(function() {
  'use strict';

  angular
    .module('jmac')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1450215881049;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('jmac')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController($scope, $location, anchorSmoothScroll) {
    var vm = this;

    function preload(arrayOfImages) {
      angular.element(arrayOfImages).each(function() {
      angular.element('<img/>')[0].src = vm;
      });
    }

    $scope.gotoElement = function (eID){
      // set the location.hash to the id of
      // the element you wish to scroll to.
      $location.hash('bottom');

      // call $anchorScroll()
      anchorSmoothScroll.scrollTo(eID);
    };

    preload([
      'assets/img/web/2-com.png',
      'assets/img/web/bulwark.png',
      'assets/img/web/columbia.png',
      'assets/img/web/cub.png',
      'assets/img/web/horace.png',
      'assets/img/web/red.png',
      'assets/img/web/vf.png',
      'assets/img/web/yanmar.png'
    ]);
  }
})();

(function() {
  'use strict';

  angular
    .module('jmac')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $location, $anchorScroll) {

    $log.debug('runBlock end');

    //Lets you jump to an id on the homepage
    $rootScope.$on('$routeChangeSuccess', function(newRoute, oldRoute, scope, next, current) {
      if($location.hash()) $anchorScroll();
      $log.debug('rootscope: ' + $rootScope.part);
      $rootScope.part = $route.current.activetab;
      $log.debug('route: ' + $route);
      $log.debug('route current: ' + $route.current);
      $log.debug('route current activetab: ' + $route.current.activetab);
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('jmac')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'home'
      })
      .state('main', {
        url: '/main',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('weather', {
        url: '/weather',
        templateUrl: 'app/weather/weather.html',
        controller: 'WeatherController',
        controllerAs: 'weather'
      })
      .state('notation', {
        url: '/notation',
        templateUrl: 'app/notation/notation.html',
        controller: 'NotationController',
        controllerAs: 'notation'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

